(function() {
  'use strict';

  angular
    .module('angular')
    .component('noosferoBlog', {
      restrict: 'E',
      templateUrl: 'app/components/noosfero-articles/blog/blog.html',
      bindings: {
          article: '<',
          profile: '<'
      },
      controller: BlogController
    });

  /** @ngInject */
  function BlogController(noosfero) {
    var vm = this;
    vm.posts = [];
    vm.perPage = 3;
    vm.currentPage = 1;

    vm.loadPage = function() {
      noosfero.articles.one(vm.article.id).customGET('children', {
        content_type: 'TinyMceArticle',
        per_page: vm.perPage,
        page: vm.currentPage
      }).then(function(response) {
        vm.totalPosts = response.headers('total');
        vm.posts = response.data.articles;
      });
    }
    vm.loadPage();
  }

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .directive('noosferoArticle', noosferoArticle);

  /** @ngInject */
  function noosferoArticle($injector, $compile) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/noosfero-articles/article/article.html',
      scope: {
          article: '<',
          profile: '<'
      },
      controller: ArticleController,
      controllerAs: 'vm',
      bindToController: true,
      link: function(scope, element) {
        var specificDirective = 'noosfero'+scope.vm.article.type;
        if($injector.has(specificDirective+'Directive')) {
          var directiveName = specificDirective.replace(/([a-z])([A-Z])/g, '$1-$2').toLowerCase();
          element.replaceWith($compile('<'+directiveName+' article="vm.article" profile="vm.profile"></'+directiveName+'>')(scope));          
        }
      }
    };

    return directive;

    /** @ngInject */
    function ArticleController() {
    }
  }

})();

