(function() {
  'use strict';
  
  angular.module('angular').factory('noosfero', function(Restangular, $q) {
    var currentProfile = $q.defer();

    return {
      currentProfile: currentProfile.promise,
      setCurrentProfile: function(profile) { currentProfile.resolve(profile) },
      profiles: Restangular.service('profiles'),
      articles: Restangular.service('articles'),
      profile: function(profileId) {
        return Restangular.one('profiles', profileId);
      },
      members: function(profile) {
        return Restangular.service('members', profile);
      },
      boxes: function(profileId) {
        return Restangular.service('boxes', Restangular.one('profiles', profileId))
      }
    }
  });
})();

(function() {
  'use strict';
  angular
    .module("angular")
    .filter("noosferoTemplateFilter", function() {
      return function(text, options) {
        for(var option in options) {
          text = text.replace('{'+option+'}', options[option]);
        }
        return text;
      }
    })
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {},
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment, $modal, AuthService, Session, $scope, $state, AUTH_EVENTS) {
      var vm = this;

      vm.currentUser = Session.getCurrentUser();
      vm.modalInstance = null;

      vm.openLogin = function() {
        vm.modalInstance = $modal.open({
          templateUrl: 'app/components/auth/login.html',
          controller: 'AuthController',
          controllerAs: 'vm',
          bindToController: true
        });
      };
      vm.logout = function() {
        AuthService.logout();
        $state.go($state.current, {}, {reload: true});  //TODO move to auth
      };
      $scope.$on(AUTH_EVENTS.loginSuccess, function() {
        if(vm.modalInstance) {
          vm.modalInstance.close();
          vm.modalInstance = null;
        }
        $state.go($state.current, {}, {reload: true}); //TODO move to auth
      });
      $scope.$on(AUTH_EVENTS.logoutSuccess, function() {
        vm.currentUser = Session.getCurrentUser();
      });

      vm.activate = function() {
        if(!vm.currentUser) vm.openLogin();
      }
      vm.activate();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .factory('Session', Session)
    .factory('AuthService', AuthService);

  /** @ngInject */
  function AuthService($q, $http, $rootScope, Session, $log, AUTH_EVENTS) {

    function login (credentials) {
      var url = '/api/v1/login';
      var encodedData = 'login=' + credentials.username + '&password=' + credentials.password;
      return $http.post(url, encodedData).then(loginSuccessCallback, loginFailedCallback);
    }

    function loginFromCookie() {
      var url = '/api/v1/login_from_cookie';
      return $http.post(url).then(loginSuccessCallback, loginFailedCallback);
    }
    
    function loginSuccessCallback(response) {
      $log.debug('AuthService.login [SUCCESS] response', response);
      var currentUser = Session.create(response.data);
      $rootScope.currentUser = currentUser;
      $rootScope.$broadcast(AUTH_EVENTS.loginSuccess, currentUser);
      return currentUser;
    }
    
    function loginFailedCallback(response) {
      $log.debug('AuthService.login [FAIL] response', response);
      $rootScope.$broadcast(AUTH_EVENTS.loginFailed);
      // return $q.reject(response);
      return null;
    }

    function logout () {
      Session.destroy();
      $rootScope.currentUser = undefined;
      $rootScope.$broadcast(AUTH_EVENTS.logoutSuccess);
      $http.jsonp('/account/logout'); //FIXME logout from noosfero to sync login state
    }

    function isAuthenticated () {
      return !!Session.userId;
    }

    function isAuthorized (authorizedRoles) {
      if (!angular.isArray(authorizedRoles)) {
        authorizedRoles = [authorizedRoles];
      }
      return (service.isAuthenticated() && authorizedRoles.indexOf(Session.userRole) !== -1);
    }

    var service = {
      login: login,
      loginFromCookie: loginFromCookie,
      logout: logout,
      isAuthenticated: isAuthenticated,
      isAuthorized: isAuthorized
    };
    return service;
  }

  /** @ngInject */
  function Session($localStorage, $log) {
    var service = {};

    service.create = function(data) {
      $localStorage.currentUser = data.user;
      $log.debug('User session created.', $localStorage.currentUser);
      return $localStorage.currentUser;
    };

    service.destroy = function() {
      delete $localStorage.currentUser;
      $log.debug('User session destroyed.');
    };

    service.getCurrentUser = function () {
      return $localStorage.currentUser;
    };

    return service;
  }

})();

