(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ProfileInfoController', ProfileInfoController);


  /** @ngInject */
  function ProfileInfoController(noosfero) {
    var vm = this;
    vm.profile = null;
    vm.activities = [];
    activate();

    function activate() {
      noosfero.currentProfile.then(function(profile) {
        vm.profile = profile;
        return noosfero.profiles.one(vm.profile.id).one('activities').get();
      }).then(function(response) {
        vm.activities = response.data.activities;
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ProfileController', ProfileController);


  /** @ngInject */
  function ProfileController(noosfero, $log, $stateParams) {
    var vm = this;
    vm.boxes = [];
    activate();

    function activate() {
      noosfero.profiles.one().get({identifier: $stateParams.profile}).then(function(response) {
        vm.profile = response.data[0];
        noosfero.setCurrentProfile(vm.profile);
        return noosfero.boxes(vm.profile.id).one().get();
      }).then(function(response) {
        vm.boxes = response.data.boxes;
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ProfileHomeController', ProfileHomeController);


  /** @ngInject */
  function ProfileHomeController(noosfero, $log, $stateParams, $scope, $state) {
    var vm = this;
    activate();

    function activate() {
      noosfero.currentProfile.then(function(profile) {
        vm.profile = profile;
        return noosfero.profile(vm.profile.id).customGET('home_page', {fields: 'path'});
      }).then(function(response) {
        if(response.data.article) {
          $state.transitionTo('main.profile.page', {page: response.data.article.path, profile: vm.profile.identifier}, {location: false});
        } else {
          $state.transitionTo('main.profile.info', {profile: vm.profile.identifier}, {location: false});
        }
      });
    }
  }
})();

