(function() {
  'use strict';

  angular
    .module('angular')
    .controller('CmsController', CmsController);


  /** @ngInject */
  function CmsController(noosfero, $stateParams, $httpParamSerializer, $state, SweetAlert) {
    var vm = this;
    vm.article = {};
    vm.profile = null;

    vm.save = function() {
      noosfero.currentProfile.then(function(profile) {
        return noosfero.profiles.one(profile.id).customPOST(
          {article: vm.article},
          'articles',
          {},
          {'Content-Type':'application/json'}
        )
      }).then(function(response) {
        $state.transitionTo('main.profile.page', {page: response.data.article.path, profile: response.data.article.profile.identifier});
        SweetAlert.swal({
          title: "Good job!",
          text: "Article saved!",
          type: "success",
          timer: 1000
        });
      });
    }
    
  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, Restangular, Session) {
    Restangular.addFullRequestInterceptor(function(element, operation, route, url, headers) {
      if(Session.getCurrentUser()) {
        headers['Private-Token'] = Session.getCurrentUser().private_token;
      }
      return { headers: headers };
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .config(routeConfig);

  function routeConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('main', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm',
        resolve: {
          currentUser: function(AuthService) {
            return AuthService.loginFromCookie();
          }
        }
      })
      .state('main.profile.cms', {
        url: '^/myprofile/:profile/cms',
        views: {
          'mainBlockContent': {
            templateUrl: 'app/cms/cms.html',
            controller: 'CmsController',
            controllerAs: 'vm'
          }
        }
      })
      .state('main.profile.settings', {
        url: '^/myprofile/:profile'
      })
      .state('main.profile', {
        url: '^/:profile',
        abstract: true,
        views: {
          'content': {
            templateUrl: 'app/profile/profile.html',
            controller: 'ProfileController',
            controllerAs: 'vm'
          }
        }
      })
      .state('main.profile.home', {
        url: '',
        views: {
          'mainBlockContent': {
            controller: 'ProfileHomeController',
            controllerAs: 'vm'
          }
        }
      })
      .state('main.profile.info', {
        url: '^/profile/:profile',
        views: {
          'mainBlockContent': {
            templateUrl: 'app/profile-info/profile-info.html',
            controller: 'ProfileInfoController',
            controllerAs: 'vm'
          }
        }
      })
      .state('main.profile.page', {
        url: '/{page:any}',
        views: {
          'mainBlockContent': {
            templateUrl: 'app/content-viewer/page.html',
            controller: 'ContentViewerController',
            controllerAs: 'vm'
          },
          'actions@main': {
            templateUrl: 'app/content-viewer/navbar-actions.html',
            controller: 'ContentViewerActionsController',
            controllerAs: 'vm'
          }
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

